  if (IconComponent) {
    return <IconComponent className={className} style={style} />;
  }

  // –ï—Å–ª–∏ —ç—Ç–æ —É–∂–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –∏–∫–æ–Ω–∫–∏ lucide (–Ω–∞–ø—Ä–∏–º–µ—Ä, "shopping-cart")
